        &__call-link {
            font-size: 18px; }
        &__button {
            margin-top: 20px; } }
    .promo-container {
        &__header {
            font-size: 40px; }
        &__subheader {
            font-size: 25px; }
        &__header-text {
            font-size: 16px;
            font-weight: 400;
            line-height: 22px; } }
    .button {
        &__blue {
            &_small {
                width: 100%;
                height: 40px;
                font-size: 13px; } } }
    .title {
        font-size: 35px; }
    .icon-box {
