@media (max-width: 767px) {
    .nav-menu {
        &__list {
            position: fixed;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            top: 0;
            left: -100%;
            width: 50%;
            height: 100vh;
            padding: 20px 15px;
            background: rgba(0, 0, 0, .8);
            transition: 0.6s all;
            &_active {
                left: 0; } }
        &__list-item {
            padding-right: 0;
            margin-bottom: 20px;
            border: none; }
        &__link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 25px;
        height: 12px;
        z-index: 3;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader-contant {
        &__call-link {
            text-align: center;
            font-size: 18px; }
        &__button {
            margin-top: 10px; } }
    .button {
        text-align: center;
        &:hover {
            pointer-events: none; }
        &__blue {
            &_small {
                width: 165px;
                height: 30px;
                font-size: 14px;
                margin-top: 5px; } } }
    .promo-container {
        &__header {
            font-size: 30px; }
        &__subheader {
            font-size: 20px; }
        &__header-text {
            font-size: 15px;
            line-height: 20px; } }
    .title {
        font-size: 28px; }
    .third-section {
        padding: 48px 0 55px 0; }
    .cards-item {
        &__box {
            width: 300px; }
        &__plus {
            right: 135px;
            &:hover {}
            pointer-events: none; }
        &__subtitle {
            font-size: 22px; } }
    .fouth-section {
        background: #070716;
        min-height: 600px;
        padding: 50px 0 100px;
        &__img {
            max-width: 450px; }
        &__img-box {
            color: #FFF;
            text-align: center;
            font-size: 25px;
            line-height: 24px; } }
    .fifth-section {
        padding: 50px 0 60px 0; }
    .list {
        &__box-blue {
            margin-bottom: 40px; } }
    .list {
        &__box {
            margin-bottom: 30px;
            &:last-child {
                margin-bottom: 0; } }
        &__box-blue {
            margin-bottom: 30px; } }
    .sixth-section {
        min-height: 575px;
        &__container {
            min-height: 575px; }
        &__subtitle {
            margin-bottom: 280px; } }
    .footer {
        &__icons-box {
            display: inline-flex;
            flex-direction: row;
            top: 64%;
            left: 50%; }
        &__mobile {
            display: inline-flex;
            flex-direction: row;
            top: 84%;
            left: 50%; } } }

